.header{
  background-color: #012246;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  align-items: center;
  height: 100px;
  padding: 0 0 20px 20px;
  .logo >img{
    max-width: 250px;
  }
  .navbar{
    // align-items: flex-end;
    box-shadow: 0 2px 23px 0 rgba(0, 0, 0, 0.1),
    0 2px 49px 0 rgba(0, 0, 0, 0.06);
    margin-left: 20px;
    height: 100%;
    width: 100%;
  }
}